<script>
    import { fade, fly } from 'svelte/transition';
    import { quintOut } from 'svelte/easing';

    let showButton = false;
    function toggleShowButton() {
        showButton = !showButton;
    }
    const flyProps = {
        y: 1000,
        opacity: 0.5,
        easing: quintOut,
    };
</script>

{#if showButton}
<button on:click="{toggleShowButton}" class="filter-btn shadow" transition:fly="{flyProps}">
    <img class="filter-icon" src="icons/filter.svg" alt="" />
</button>
{:else}
<div class="popup shadow" transition:fly="{flyProps}">
    <button on:click="{toggleShowButton}" class="close">Close</button>
</div>
{/if}
<style>
    .filter-btn {
        position: fixed;
        bottom: 25px;
        right: 25px;

        width: 64px;
        height: 64px;
        margin: 0;
        border-radius: 50%;
        padding: 1em;
    }
    .filter-icon {
        width: 100%;
    }
    .popup {
        padding: 1em;
        position: fixed;
        bottom: 1%;
        left: 50%;
        transform: translateX(-50%);
        background-color: white;
        width: 400px;
        max-width: 80%;
        height: 50vh;

        display: flex;
        flex-direction: column;
    }
    .shadow {
        box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.3);
    }
    .close {
        margin-top: auto;
    }
</style>
